      width: 50%;
      height: auto;
    }
    #rubygym {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 50%;
      height: auto;
    }
    #eri {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 50%;
      height: auto; 
    }
      
        
    
  }
    

  
    
    
  
  